// scripts for the order modal
import { postOrder } from './modalAPI';
import notification from './sweetAlert';
import baseModal from './modal';

const orderForm = {
  animalId: null,
  getMarkup() {
    return `<form class="order-form js-order-form">
                <h2 class="order-title">–ó–∞–ª–∏—à—ñ—Ç—å –∑–∞—è–≤–∫—É –Ω–∞ –∑–Ω–∞–π–æ–º—Å—Ç–≤–æ</h2>
                <label for="name">–Ü–º‚Äô—è*</label>
                <input type="text" id="name" name="name" placeholder="–ê–Ω–¥—Ä—ñ–π">
                <label for="phone">–¢–µ–ª–µ—Ñ–æ–Ω*</label>
                <input type="text" name="phone" id="phone" placeholder="+38 (095) 555 99 22">
                <label for="comment">–ö–æ–º–µ–Ω—Ç–∞—Ä</label>
                <textarea type="textarea"  name="comment" id="comment" placeholder="–ù–∞–ø–∏—à—ñ—Ç—å –≤–∞—à –∫–æ–º–µ–Ω—Ç–∞—Ä"></textarea>
               <div class='order-controllers-container'> <button type="submit" class="dark">–ù–∞–¥—ñ—Å–ª–∞—Ç–∏</button></div>
            </form>`;
  },
  listnerHandler() {
    const orderForm = document.querySelector('.js-order-form');
    orderForm.addEventListener('submit', async e => {
      e.preventDefault();
      const form = new FormData(orderForm);
      const params = {
        name: form.get('name').trim(),
        phone: form.get('phone').trim(),
        animalId: this.animalId,
      };
      form.get('comment').trim() &&
        (params.comment = form.get('comment').trim());
      const validationPassed = this.fieldsValidation(params);
      if (!validationPassed) {
        return;
      }
      const data = await postOrder(params);
      e.target.reset();
      baseModal.closeModal();
      notification.openSuccessAlert(
        `${params.name}, ${data.animalName} –∑ –Ω–µ—Ç–µ—Ä–ø—ñ–Ω–Ω—è–º —á–µ–∫–∞—î –Ω–∞ –∑—É—Å—Ç—Ä—ñ—á.`
      );
    });
  },
  fieldsValidation(params) {
    if (params.name === '' || params.phone === '') {
      notification.errorAlert(`–ó–∞–ø–æ–≤–Ω—ñ—Ç—å, –±—É–¥—å –ª–∞—Å–∫–∞ –Ü'–º—è —Ç–∞ –¢–µ–ª–µ—Ñ–æ–Ω.`);
      return false;
    }
    if (params.name.length > 32) {
      notification.errorAlert(
        `–ú–∞–∫—Å–∏–º–∞–ª—å–Ω–∞ –¥–æ–≤–∂–∏–Ω–∞ –Ü'–º—è –Ω–µ –º–∞—î –ø–µ—Ä–µ–≤–∏—â—É–≤–∞—Ç–∏ 32 —Å–∏–º–≤–æ–ª–∏.`
      );
      return false;
    }
    if (!params.phone.match(/^[0-9]{12}$/)) {
      notification.errorAlert(`–¢–µ–ª–µ—Ñ–æ–Ω –º–∞—î —Å–∫–ª–∞–¥–∞—Ç–∏—Å—è –∑ 12 —Ü–∏—Ñ—Ä.`);
      return false;
    }
    if (params.comment && params.comment.length > 500) {
      notification.errorAlert(
        `–ú–∞–∫—Å–∏–º–∞–ª—å–Ω–∞ –¥–æ–≤–∂–∏–Ω–∞ –ö–æ–º–µ–Ω—Ç–∞—Ä—è –Ω–µ –º–∞—î –ø–µ—Ä–µ–≤–∏—â—É–≤–∞—Ç–∏ 500 —Å–∏–º–≤–æ–ª—ñ–≤. –ó–∞—Ä–∞–∑ –≤—ñ–Ω ${params.comment.length} —Å–∏–º–≤–æ–ª—ñ–≤.`
      );
      return false;
    }
    return true;
  },
};
export default orderForm;
